import React from 'react';
import './App.css';
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
// import { LoggedIn } from './components/LoggedIn/LoggedIn';
import { VerifyEmail } from './components/LoggedOut/Register/VerifyEmail';
import EmailVerified from './components/LoggedOut/Register/EmailVerified';
import Home from './components/LoggedOut/Home/Home';
import Login from './components/LoggedOut/Login/Login';
import Register from './components/LoggedOut/Register/Register';
import Navigation from './components/LoggedOut/Navigation/Navigation';
import Events from './components/LoggedOut/Events/Events';
import EventPage from './components/LoggedOut/Events/EventPage';
import EventRegister from './components/LoggedOut/Events/EventRegister';
import Activities from './components/LoggedOut/Activities/Activities';
import ActivityForm from './components/LoggedOut/Activities/ActivityForm';
// import {AuthContext} from './context/AuthContext';
import GalleryPage from './components/LoggedOut/Gallery/GalleryPage';

function App() {
  // const {token} = useContext(AuthContext);

  return (
    <div className="App">
      <Router>
        <Navigation/>
        <Switch>
            {/* {token !== null? <Redirect to="/u/landing"/> :  */}
            
            {/* } */}
            <Route exact path="/">
              <Home/>
            </Route>
            <Route exact path="/WCE-Connects-Frontend/gallery">
              <GalleryPage/>
            </Route>
            <Route exact path="/WCE-Connects-Frontend/activities">
              <Activities/>
            </Route>
            <Route exact path="/WCE-Connects-Frontend/activities/form">
              <ActivityForm/>
            </Route>
            <Route exact path="/WCE-Connects-Frontend/events">
              <Events/>
            </Route>
            <Route exact path="/WCE-Connects-Frontend/events/eventid">
              <EventPage/>
            </Route>
            <Route exact path="/WCE-Connects-Frontend/events/eventid/register">
              <EventRegister/>
            </Route>
            <Route exact path='/WCE-Connects-Frontend/login'>
              <Login/>
            </Route>
            <Route exact path="/WCE-Connects-Frontend/register">
              <Register/>
            </Route>
            <Route exact path="/WCE-Connects-Frontend/verify">
              <VerifyEmail/>
            </Route>
            <Route exact path="/WCE-Connects-Frontend/verified">
              <EmailVerified/>
            </Route>
        </Switch>
        {/* <Route path="/u"> 
          <LoggedIn/>
        </Route> */}
      </Router>
    </div>
  );
}

export default App;
